import React, { useState, useRef, useEffect, memo } from 'react';
import useFetch from '../util/useFetch';
import styled from 'styled-components';

const StyledAddForm = styled.div`
  
  button::selection{
    background: none;
  }
  input::selection{
    background: none;
  }
  form{
    display: flex;
    justify-content: center;
    align-items: center;
  }
  input{
    width: 270px;
    height: 30px;
    background-color: rgba(255, 201, 54, 0.3);
    border: none;
    border-radius: 5px 0 0 5px;
  }
  
  button{
    width: 65px;
    height: 32px;
    border: none;
    background-color: rgba(1, 107, 8, 0.4);
    font-weight: bold;
    font-family:  "NanumRg";
    border-radius: 0 5px 5px 0;
  }
  button:hover{
    cursor: pointer;
  }
`

const AddForm = memo(({ addTodo }) => {
  const [value, setValue] = useState('');
  // const [todos, isPending, error] = useFetch(`http://localhost:3001/todos`)
  // Ìï†Ïùº ÏûÖÎ†•Ïπ∏(input)Ïóê ÏûêÎèôÏúºÎ°ú Ìè¨Ïª§Ïä§, ÏûÖÎ†• ÌõÑ Ï¥àÍ∏∞Ìôî ÏãúÏºúÏ£ºÎäî ÏΩîÎìú
  const input = useRef(null);

  useEffect(() => {
    input.current.focus();
    setValue('');
  }, [addTodo]);

  const onChangeInput = (e) => {
    setValue(e.target.value);
  };

  return (
    <StyledAddForm>
      <form className="add-form">
        <input ref={input} value={value} onChange={onChangeInput} placeholder={`Ïò§ÎäòÏùò Ìï† Ïùº ÏûÖÎ†•ÌïòÍ∏∞ !`} />
        <button type="submit" onClick={addTodo(value)}>
          Ï∂îÍ∞Ä
        </button>
      </form>
    </StyledAddForm>
  );
});

export default AddForm;